window.umps_widget = {
  update: () => {
    // localStorage.setItem('UMPS_cache', JSON.stringify(window.umps_data));
    window.umps_widget.menuCreator();
  },
  dataManage: () => {
    /*
    if (!window.umps_data) {
      document.getElementById('UMPS_console').innerHTML = 'Error en los datos';
    } else {
      document.getElementById('UMPS_console').innerHTML = JSON.stringify(window.umps_data);
    }
    */
    const tools = (window.umps_data) ? window.umps_data.tools || [] : [];
    if (tools.length > 0) {
      document.getElementById('UMPS_menu_load_holder').innerHTML = tools.map(
        el => `<a class="UMPS_menu_load_item" href="${el.url}">
          <span class="UMPS_item_icon" style="background-image:${(el.icon === '') ? 'none' : `url('${el.icon}')`};"></span>
          <span class="UMPS_item_name">${el.name}</span>
        </a>`,
      ).join('').concat('<a href="#" id="UMPS_menu_load">MÃ¡s servicios</a>');
    } else {
      document.getElementById('UMPS_menu_load_holder').innerHTML = `<div>
        <p>No hemos podido acceder a tu cuenta UM en estos momentos</p>
        <a href="//entrada.um.es" target="blank" id="UMPS_menu_load">Accede a tu cuenta UM</a>
      </div>`;
    }
  },
  loadData: () => {
    const data = null; // localStorage.getItem('UMPS_cache');
    if (data === null) {
      // const URL = 'http://atica-67-237.atica.um.es:8001/pruebaIntercambio/rest/json';
      const URL = document.getElementById('UMPS_menu').getAttribute('data-private-url');
      if (document.getElementById('UMPS_iframe') === null) {
        const iframe = document.createElement('iframe');
        iframe.setAttribute('src', URL);
        iframe.setAttribute('id', 'UMPS_iframe');
        iframe.setAttribute('width', 0);
        iframe.setAttribute('height', 0);
        iframe.style.display = 'none';
        iframe.addEventListener('load', () => {
          const head = document.getElementsByTagName('head').item(0);
          const script = document.createElement('script');
          script.setAttribute('type', 'text/javascript');
          script.setAttribute('src', URL);
          script.setAttribute('defer', 'defer');
          head.appendChild(script);
        });
        document.body.appendChild(iframe);
      }
    } else {
      window.umps_data = JSON.parse(data);
    }
  },
  menuCreator: () => {
    const menuHolder = document.getElementById('UMPS_menu');
    if (menuHolder) {
      menuHolder.innerHTML = `
        <span id="UMPS_menu_toggler"></span>
        <nav id="UMPS_menu_dynamic" style="display:none">
          <span class="UMPS_menu_dynamic_title">Portal de servicios</span>
          <div id="UMPS_menu_load_holder"></div>
        </nav>
        <span id="UMPS_menu_style"></span>
      `;
      document.getElementById('UMPS_menu_toggler').addEventListener('click', (evt) => {
        evt.target.classList.toggle('umps_menu__active');
        const menu = document.getElementById('UMPS_menu_dynamic');
        const isHidden = menu.style.display === 'none';
        const isLoaded = document.getElementById('UMPS_menu_load_holder').innerText === '';
        menu.style.display = isHidden ? 'block' : 'none';
        if (isLoaded && isHidden) {
          window.umps_widget.dataManage();
        }
      });
    }
  },
  init: () => {
    window.umps_widget.loadData();
  },
};

document.addEventListener('DOMContentLoaded', () => {
  window.umps_widget.init();
});
